//RTKQuery
import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/dist/query/react";
import { IPost } from "../models/IPost";

export const postAPI = createApi({
  reducerPath: "postAPI", // ,     
  baseQuery: fetchBaseQuery({
    //!url servera (   (   db.json -> npm install -g json-server -> json-server --watch db.json --port 5000 .https://github.com/typicode/json-server))
    baseUrl: "http://localhost:5000",
    /*  baseUrl: "https://jsonplaceholder.typicode.com/", */
  }), //  url       
  tagTypes: ["Post"], // toolkit     
  //       
  //  useFetchAllUPostsQuery( fetchAllUPosts)  useCreatePostMutation( createPost)
  //  postAPI.useFetchAllUPostsQuery
  endpoints: (build) => ({
    //(query(get) -    , mutation -   )
    fetchAllUPosts: build.query<IPost[], number>({
      query: (limit: number = 2) => ({
        url: "/posts", //   
        params: {
          _limit: limit, // query parametrs     
        },
      }),
      // toolkit     ( endpoin    )
      providesTags: (result) => ["Post"],
    }),
    //!   mutation(post)
    // 
    createPost: build.mutation<IPost, IPost>({
      query: (post) => ({
        url: "/posts",
        method: "POST",
        body: post,
      }),
      // toolkit     ( endpoin      )
      invalidatesTags: ["Post"],
    }),

    // (put)
    updatePost: build.mutation<IPost, IPost>({
      query: (post) => ({
        url: `/posts/${post.id}`,
        method: "PUT",
        body: post,
      }),
      invalidatesTags: ["Post"],
    }),

    // (delete)
    deletePost: build.mutation<IPost, IPost>({
      query: (post) => ({
        url: `/posts/${post.id}`,
        method: "DELETE",
      }),
      invalidatesTags: ["Post"],
    }),
  }), //endpoint-         
});
